<template>
  <div>
    <audio id="audio"></audio>
    <div class="iconfont icon-sound"></div>
    <el-progress :show-text="false" :stroke-width="5" :percentage="Recordingtage" color="#2E70ED"></el-progress>
    <div class="time-text">{{Recordingtime}}</div>
    <el-button class="record-btn" @click="startRecording" v-if="!bengin">开始录音</el-button>
    <el-button class="record-btn" @click="endRecording" v-if="bengin">结束录音</el-button>
  </div>
</template>

<script>
// import '@/assets/js/recorder.mp3.min';
// import '../js/luyin'

// import Recorder from "recorder-js";    // 引入录音插件
// const audioContext = new (window.AudioContext || window.webkitAudioContext)();
// const recorder = new Recorder(audioContext, {
//   // An array of 255 Numbers
//   // You can use this to visualize the audio stream
//   // If you use react, check out react-wave-stream
//   onAnalysed: data => {
//     console.log("recorder data",data, 321)
//   }
// });

// console.log(recorder);

import "@/assets/recordermaster/src/recorder-core.js";

export default {
  name: "recorderDemo",
  data() {
    return {
      bengin: false,
      Recordingtime: "00",
      Recordingtage: "30",
      Recordingnum: ""
    };
  },
  created() {},
  methods: {
    startRecording() {
      this.bengin = true;
      recopen();
    },
    endRecording() {
      this.bengin = false;
      recstop();
    }
  }
};

var rec;
var r = null;
var time = 0;
var timeInterval = null;
console.log(time);
function recopen() {
  // 打开录音
  if (!top.plus) {
    var type = "mp3"; // 类型
    var bit = 16; // kbps mp3、wav取值8位16位
    var sample = 16000; // mp3标准值，wav任意
    rec = window.Recorder({
      type: type,
      bitRate: bit,
      sampleRate: sample,
      onProcess: function(buffers, level, time, sampleRate) {
        console.log(buffers);
        console.log(level);
        console.log(time);
        console.log(sampleRate);
      }
    });
    rec.open(
      function() {
        console.log("已打开:" + type + " " + bit + "kbps");
        setTimeout(function() {
          // 正常打开录音后开始录音
          recstart();
        }, 200);
      },
      function(e, isUserNotAllow) {
        console.log(
          (isUserNotAllow ? "UserNotAllow，" : "") + "打开失败：" + e
        );
      }
    );
  } else {
    //alert('开始录音');
    //top.outSet('开始录音：');
    r = top.plus.audio.getRecorder();
    if (r == null) {
      //alert('录音对象未获取');
      return;
    }
    r.record(
      {
        filename: "_doc/audio/",
        format: "wav"
      },
      function(p) {
        alert("录音完成");
        console.log("录音完成：", p);
        // Audio2dataURL(p);
      },
      function(e) {
        console.log(e);
        //alert('录音失败：' + e.message);
      }
    );
    time = 0;
    if (timeInterval) {
      clearInterval(timeInterval);
    }
    timeInterval = setInterval(function() {
      time++;
    }, 1000);
  }
}

function recstop(batCall) {
  if (!top.plus) {
    if (rec) {
      if (!batCall) {
        console.log("正在编码" + rec.set.type + "...");
      }
      // var t1 = Date.now();
      rec.stop(
        function(blob, time) {
          var reader = new FileReader();
          reader.onloadend = function() {
            var base64url = reader.result;
            console.log(time);
            console.log(base64url);
            // uploadByBase64(
            //   base64url,
            //   function(data) {
            //     var newData = data.data;
            //     console.log(newData);
            //     // addVideo(newData, time);
            //   },
            //   {
            //     suffix: "mp3"
            //   }
            // );
          };
          reader.readAsDataURL(blob);
        },
        function(s) {
          console.log("失败：" + s);
          batCall && batCall();
        }
      );
    }
    setTimeout(function() {
      recclose();
    }, 200);
  } else {
    if (timeInterval) {
      clearInterval(timeInterval);
    }
    timeInterval = null;
    r.stop();
  }
}

function recstart() {
  if (rec) {
    rec.start();
    console.log("录制中...");
  }
}
function recclose() { // 关闭网页录音功能
	if (rec) {
		rec.close(function() {
			console.log("已关闭");// 控制台提示已关闭
		});
	}
}
</script>

<style scoped>
</style>
